/**
 * Job Search Resources Page JavaScript
 * 
 * This file contains the JavaScript for the Job Search Resources page.
 */

// Import components
import Accordion from '../components/accordion';
import Testimonials from '../components/testimonials';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Job Search Resources page initialized');
  
  // Initialize accordion
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });
  
  // Initialize testimonials
  Testimonials.init({
    testimonialSelector: '.testimonial',
    dotSelector: '.testimonial-dot',
    activeClass: 'active',
    autoRotate: true,
    autoRotateInterval: 5000
  });
  
  // Set up job search filters
  setupJobSearchFilters();
  
  // Set up job application tracker
  setupJobApplicationTracker();
}

/**
 * Set up job search filters
 */
function setupJobSearchFilters() {
  const filterForm = document.querySelector('.job-search-filters');
  const jobListings = document.querySelectorAll('.job-listing-item');
  
  if (filterForm && jobListings.length > 0) {
    filterForm.addEventListener('submit', function(e) {
      e.preventDefault();
      
      // Get filter values
      const industry = filterForm.querySelector('select[name="industry"]').value;
      const location = filterForm.querySelector('select[name="location"]').value;
      const jobType = filterForm.querySelector('select[name="job-type"]').value;
      
      // Filter job listings
      filterJobListings(industry, location, jobType);
    });
    
    // Reset filters
    const resetButton = filterForm.querySelector('.reset-filters-btn');
    if (resetButton) {
      resetButton.addEventListener('click', function() {
        filterForm.reset();
        
        // Show all job listings
        jobListings.forEach(listing => {
          listing.style.display = 'block';
        });
        
        // Update results count
        updateResultsCount(jobListings.length);
      });
    }
  }
  
  /**
   * Filter job listings based on criteria
   */
  function filterJobListings(industry, location, jobType) {
    let visibleCount = 0;
    
    jobListings.forEach(listing => {
      const listingIndustry = listing.getAttribute('data-industry');
      const listingLocation = listing.getAttribute('data-location');
      const listingJobType = listing.getAttribute('data-job-type');
      
      // Check if listing matches all selected filters
      const industryMatch = industry === 'all' || listingIndustry === industry;
      const locationMatch = location === 'all' || listingLocation === location;
      const jobTypeMatch = jobType === 'all' || listingJobType === jobType;
      
      // Show or hide listing based on filter match
      if (industryMatch && locationMatch && jobTypeMatch) {
        listing.style.display = 'block';
        visibleCount++;
      } else {
        listing.style.display = 'none';
      }
    });
    
    // Update results count
    updateResultsCount(visibleCount);
  }
  
  /**
   * Update the results count display
   */
  function updateResultsCount(count) {
    const resultsCount = document.querySelector('.job-results-count');
    if (resultsCount) {
      resultsCount.textContent = `${count} job${count !== 1 ? 's' : ''} found`;
    }
  }
}

/**
 * Set up job application tracker
 */
function setupJobApplicationTracker() {
  const trackerForm = document.querySelector('.job-tracker-form');
  
  if (trackerForm) {
    trackerForm.addEventListener('submit', function(e) {
      e.preventDefault();
      
      // Get form data
      const company = trackerForm.querySelector('input[name="company"]').value;
      const position = trackerForm.querySelector('input[name="position"]').value;
      const date = trackerForm.querySelector('input[name="application-date"]').value;
      const status = trackerForm.querySelector('select[name="status"]').value;
      
      // Add to tracker list
      addApplicationToTracker(company, position, date, status);
      
      // Reset form
      trackerForm.reset();
    });
  }
  
  /**
   * Add application to job tracker
   */
  function addApplicationToTracker(company, position, date, status) {
    const trackerList = document.querySelector('.job-tracker-list');
    
    if (trackerList) {
      const applicationItem = document.createElement('div');
      applicationItem.className = 'job-application-item';
      
      applicationItem.innerHTML = `
        <div class="application-header">
          <h4>${position}</h4>
          <span class="application-company">${company}</span>
        </div>
        <div class="application-details">
          <span class="application-date">Applied: ${date}</span>
          <span class="application-status status-${status.toLowerCase()}">${status}</span>
        </div>
        <div class="application-actions">
          <button class="status-update-btn" aria-label="Update status">
            <i class="fas fa-edit"></i>
          </button>
          <button class="application-delete-btn" aria-label="Delete application">
            <i class="fas fa-trash"></i>
          </button>
        </div>
      `;
      
      // Add status update functionality
      const statusUpdateButton = applicationItem.querySelector('.status-update-btn');
      statusUpdateButton.addEventListener('click', function() {
        const newStatus = prompt('Update application status:', status);
        if (newStatus) {
          const statusElement = applicationItem.querySelector('.application-status');
          statusElement.textContent = newStatus;
          statusElement.className = `application-status status-${newStatus.toLowerCase()}`;
        }
      });
      
      // Add delete functionality
      const deleteButton = applicationItem.querySelector('.application-delete-btn');
      deleteButton.addEventListener('click', function() {
        applicationItem.remove();
      });
      
      // Add to list
      trackerList.appendChild(applicationItem);
    }
  }
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
